import React, { useEffect } from 'react'
import * as styles from './style.module.scss'

const TableContent: React.FC = ({ toc }) => {
  useEffect(() => {
    const observer = new IntersectionObserver(
      (entries) => {
        entries.forEach((entry) => {
          const id = encodeURI(entry.target.getAttribute('id')) // í•œê¸€ url ì¸ì½”ë”©
          const targetContent = document.querySelector(`.toc a[href="#${id}"]`) // tocì—ì„œ í•´ë‹¹ idë¥¼ ê°€ì§„ íƒœê·¸ ì„ íƒ
          if (targetContent) {
            if (entry.intersectionRatio > 0) {
              targetContent.classList.add(styles.active)
            } else {
              targetContent.classList.remove(styles.active)
            }
          }
        })
      },
      { rootMargin: `0% 0% -85% 0%` } // ê´€ì¸¡ ì˜ì—­: TOP, RIGHT, BOTTOM, LEFT
    )
    // íŠ¸ëž˜í‚¹í•˜ê³  ì‹¶ì€ íƒœê·¸ë“¤ ì„ íƒ
    document
      .querySelectorAll(
        '.markdown-body h1, .markdown-body h2, .markdown-body h3, .markdown-body h4'
      )
      .forEach((section) => {
        observer.observe(section)
      })

    return () => {
      observer.disconnect()
    }
  }, [])

  return (
    <div
      className={`toc ${styles.toc}`}
      dangerouslySetInnerHTML={{ __html: toc }}
    />
  )
}

export default TableContent
